@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{background-color:black}.container{height:500px;background-color:black}.welcome-container,.chatagent-container{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%)}.welcome-message{margin:0;font-size:50px;text-align:center;color:white}*,*::before,*::after{box-sizing:border-box}:root{--color-primary: white;--color-secondary: #eeeae9;--color-tertiary: #d7d0cf;--color-quaternary: #f9e8e5;--color-quinary: #f5ddd9}body{min-height:100vh;font-family:canada-type-gibson, sans-serif;font-weight:300;font-size:1.25rem;display:flex;flex-direction:column;justify-content:center;overflow:hidden;background-color:black}.content{display:flex;align-content:center;justify-content:center}.text_shadows{text-shadow:3px 3px 0 #eeeae9,6px 6px 0 #d7d0cf,9px 9px #f9e8e5,12px 12px 0 #f5ddd9;text-shadow:3px 3px 0 var(--color-secondary),6px 6px 0 var(--color-tertiary),9px 9px var(--color-quaternary),12px 12px 0 var(--color-quinary);font-family:Poppins, sans-serif;font-weight:400;text-transform:uppercase;font-size:calc(2rem + 5vw);text-align:center;margin:0;color:white;color:var(--color-primary);-webkit-animation:shadows 2s ease-in infinite, move 2s ease-in infinite;animation:shadows 2s ease-in infinite, move 2s ease-in infinite;letter-spacing:0.4rem}@-webkit-keyframes shadows{0%{text-shadow:none}10%{text-shadow:3px 3px 0 #eeeae9;text-shadow:3px 3px 0 var(--color-secondary)}20%{text-shadow:3px 3px 0 #eeeae9,6px 6px 0 #d7d0cf;text-shadow:3px 3px 0 var(--color-secondary),6px 6px 0 var(--color-tertiary)}30%{text-shadow:3px 3px 0 #eeeae9,6px 6px 0 #d7d0cf,9px 9px #f9e8e5;text-shadow:3px 3px 0 var(--color-secondary),6px 6px 0 var(--color-tertiary),9px 9px var(--color-quaternary)}40%{text-shadow:3px 3px 0 #eeeae9,6px 6px 0 #d7d0cf,9px 9px #f9e8e5,12px 12px 0 #f5ddd9;text-shadow:3px 3px 0 var(--color-secondary),6px 6px 0 var(--color-tertiary),9px 9px var(--color-quaternary),12px 12px 0 var(--color-quinary)}50%{text-shadow:3px 3px 0 #eeeae9,6px 6px 0 #d7d0cf,9px 9px #f9e8e5,12px 12px 0 #